<template>
  <div id="crossword">
    <table id='grid' :style="tableStyle">
      <tbody>
        <tr v-for="y in grid" v-bind:key="y">
          <td v-for="x in y" v-bind:key="x" @click="highlight">{{x}}</td>
        </tr>
      </tbody>
    </table>
    
    <div v-if="grid.length == 0">
      <input v-model="size"/>
      <button @click="create">create</button>
    </div>
    <ul>
      <li v-for="word in words"
          v-bind:key="word.pattern"
          :style="word.style"
          @found="found">{{word.pattern}}</li>
    </ul>
    <button v-if="restart" @click="reset">New Game</button>
  </div>
</template>

<script>
import * as b from '../assets/js/crossword.js'


export default {
  name: 'CrossWord',
  props: {
    source: String
  },
  data() {
    return {
      size: 7,
      grid: [],
      words: [],
      tableStyle: {
        width: "70%",
        fontSize: "1.1em"
      },
      activeSearch: [],
      coord: [],
      restart: false
    }
  },
  methods: {
    tableSize(size) {
      return size * 55 / 2
    },
    create() {
      const self = this
      const size = parseInt(this.size)
      b
      const url = "http://localhost:3000/request?max=" + this.size + "&min=3&limit="+ this.size * 2
      fetch(url)
        .then(
          function(response) {
            if (response.status !== 200) {
              console.log('Looks like there was a problem. Status Code: ' +
                          response.status);
              return;
            }
            
            // Examine the text in the response
            response.json().then(function(data) {
              console.log(data);
              self.$emit('hide-logo')
              const res = window.crossword.core.create(data, {"max-length": size})

              self.grid = res.grid;
              self.words = res.words.map(function(e){ 
                e.style = {textDecoration: "unset"}; return e});
              self.tableStyle = {
                width: self.tableSize(self.size) + "px",
                height: self.tableSize(self.size) + "px",
                fontSize: 1.1 + "em"
              }
            });
          }
        )
        .catch(function(err) {
          console.log('Fetch Error :-S', err);
        });
    },
    reset() {
      this.grid = []
      this.words = []
      this.restart = false
      
      this.create()
    },
    highlight(event) {
      const verify = function (wordSeq, words) {
        const word = wordSeq.map(function(e) { return e.innerText }).join('')
        return words.map(function(e) {return e.pattern}).includes(word);
      }

      const findWord = function(coords){
        const range = function(start, end) {
          var r = []
          for (var i= start; i <= end; i++) { r.push(i) }
          return r;
        }

        let seq = []
        const table = document.getElementById('grid')
        if (coords[0][0] === coords[1][0]){
          //vertical
          seq = range(coords[0][1], coords[1][1]).map(function(e) {
            return table.rows[e].cells[coords[0][0]]
          })
        }
        else{
          //horizontal
          var row = table.rows[coords[0][1]]
          seq = range(coords[0][0], coords[1][0]).map(function(e){
            return row.cells[e]
          })
        }
        return seq;
      }

      const trigger = function(word) {
        const event = new Event('found');
        const li = [].slice.call(document.getElementsByTagName('li'))
          .filter(elt => elt.innerText == word);
        li[0].dispatchEvent(event)
      }

      this.coord.push(
        [event.target.cellIndex,
         event.target.parentElement.rowIndex])
      event.target.classList.add("active")

      if (this.coord.length == 2) {
        const wordSeq = findWord(this.coord)
        if (verify(wordSeq, this.words)) {
          const word = wordSeq.map(function(e) { return e.innerText }).join('')
          trigger(word)
          wordSeq.forEach(function(e){
            e.classList.remove("active");
            e.classList.add("found");
          })
        }else{
          // when word exists in dict add has bonus
          wordSeq.forEach(function(e){
            e.classList.remove("active");
          })
        }
        this.coord = []
      }
    },
    found(event) {
      const word = this.words.filter(elt => elt.pattern == event.target.innerText);
      word[0].found = true
      word[0].style = {textDecoration: "line-through"}
      if (this.words.filter(w => w.found).length == this.words.length) {
        this.restart = true
      }
    }
  }
}
</script>

<style>
#crossword {
    width: 100%;
    padding: 2em;
}

#crossword table {
   background-color: #f8851f;
   margin: 0 auto;
}
#crossword table td {
    width: 7em;
    text-align: center;
}
#crossword .active {
    background: #ecf0c0;
}

#crossword .found {
    background: #b1e6aa;
}
</style>
